import React,{ Component } from 'react';

import { Link } from 'react-router-dom';
import { Menu } from 'antd';

class IndexMenu extends Component {

    render() {

        let {mode,id} = this.props;
        return (

            <Menu id={id} mode={mode}>
                <Menu.Item>
                    <Link to='/index/all'></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to='/index/good'></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to='/index/ask'></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to='/index/share'></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to='/index/job'></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to='/index/dev'></Link>
                </Menu.Item>
            </Menu>

        );

    }

}

export default IndexMenu;


